@import "../../node_modules/bulma/sass/utilities/_all";

a-scene {
  position: fixed !important;
  z-index: -1;
}

#enter-vr, #enter-ar{
  position: absolute;
  left: 0;
  top: 0;
  width: 58px;
  height: 34px;
  margin: 10px;
  z-index: 1;
}

#enter-ar{
  top: 34px;
}

@media (min-width: $tablet) {
  .nave {
    float: left;
    object-fit: cover;
    width: 60%;
    max-width: 420px;
    min-width: 250px;
    clip-path: polygon(0.14% 0.34%, 51.94% 0.2%, 65.4% 10.02%, 76.12% 26.02%, 84.21% 39.96%, 86.68% 40.56%, 92.28% 44.41%, 97.7% 54.42%, 99.39% 66.29%, 95.63% 84.67%, 74.77% 84.12%, 57.62% 96.08%, 46.46% 92%, 12.86% 84.43%, 0.05% 64.76%);
    shape-outside: polygon(0.14% 0.34%, 51.94% 0.2%, 65.4% 10.02%, 76.12% 26.02%, 84.21% 39.96%, 86.68% 40.56%, 92.28% 44.41%, 97.7% 54.42%, 99.39% 66.29%, 95.63% 84.67%, 74.77% 84.12%, 57.62% 96.08%, 46.46% 92%, 12.86% 84.43%, 0.05% 64.76%);
  }
}

.logo{
  display: block;
  margin: 0 auto;
  padding: 50px 0;
}

.facsa{
  background-color: rgba(#0a0a0a,0.3);
  color: white;
  .content {
    text-align: justify;
  }

  @media (min-width: $tablet) {
    .story1 {
      width: 300px;
      float: left;
      margin: 0 10px 10px 0;
    }

    .story2 {
      width: 300px;
      float: right;
      margin: 10px 0 10px 10px;
    }
  }
}

#asistente{
  --poster-color: transparent;
  width: 100%;
  height: 100%;
}

.second-row {
  flex-grow: 1;
}

.content.with-flex-rows{
  display: flex;
  flex-direction: column;
  height: 100%;
}